import React from "react"
import { Link } from "gatsby"

const PostList = ({ location, posts, withAuthor }) => {
  return (
    <ol className="no-style">
      {posts.map(tab => {
        const title = tab.frontmatter.title || tab.fields.slug

        return (
          <li key={tab.fields.slug} className="post-list-item">
            <div className="meta">
              <time dateTime={tab.frontmatter.date}>
                {tab.frontmatter.date}
              </time>
            </div>
            <Link
              to={tab.fields.slug}
              itemProp="url"
              state={{ prevPath: location.pathname, prevTitle: "Home" }}
            >
              {withAuthor && (
                <span>
                  {tab.frontmatter.author} ~ {title}
                </span>
              )}
              {!withAuthor && <span>{title}</span>}
            </Link>{" "}
          </li>
        )
      })}
    </ol>
  )
}

export default PostList
